let state = {
  user: JSON.parse(localStorage['user']||"{}")
}

let getters = {}
for (let key in state) {
  getters[key] = state => state[key]
}

const actions = {
  updateManager({
    commit
  }) {
    commit('updateManager')
  },
  updateUser({
    commit,
    state
  }, info) {
    commit('updateUser', info)
  },
}

const mutations = {
  updateManager(state) {
    console.log('111111')
  },
  updateUser(state, info){
    state.user = info
  }
}

module.exports = {
  state,
  getters,
  actions,
  mutations
}



