import { api } from "@/utils/api.js";
import { mapActions, mapMutations } from "vuex";
export default {
  name: "login",
  props: {},
  components: {},
  data() {
    return {
      url: "",
      isWX: false,
      phone: "",
      password: ""
    };
  },
  created() {
    let _url = window.location.search;
    this.url = _url.split("?url=")[1]; //登录回退页
    this.isWX = this.isWeiXin();
    console.log(this.$store.state.user);
  },
  methods: {
    ...mapMutations(["updateManager"]),
    ...mapActions(["updateUser"]),
    // 校验信息
    async checkInfo() {
      let that = this;
      let reg = /^1[0-9]{10}$/;
      let _test = reg.test(this.phone);
      if (!_test) {
        this.$vux.toast.text("请输入正确的手机号");
        return;
      }
      if (!this.password) {
        this.$vux.toast.text("请输入密码");
        return;
      }
      if (!(this.password.length > 5 && this.password.length < 12)) {
        this.$vux.toast.text("密码长度不正确");
        return;
      }
      let data = {
        plat: 3,
        tel: this.phone,
        pwd: this.$encrypt(this.password)
      };
      this.$vux.loading.show({
        text: "Loading"
      });
      const [err, res] = await api.login(data);
      if (err) {
        this.$vux.loading.hide();
        if (err.code === 4004) {
          this.$vux.toast.text("该账号尚未注册,清先注册");
          return;
        }
        this.$vux.toast.text(err.msg, "top");
        return;
      }
      this.$vux.loading.hide();
      this.$vux.toast.text("登录成功", "top");
      // 存储token
      this.updateUser(res.data);
      localStorage["user"] = JSON.stringify(res.data);
      localStorage["account"] = res.data.account;
      localStorage["token"] = res.data.token;
      if (this.url) {
        window.location.href = this.url;
      } else {
        this.$router.push("/");
      }
    },
    //微信登录
    wxLogin() {
      let fromurl = window.location.href;
      window.location.href =
        "https://open.weixin.qq.com/connect/oauth2/authorize?appid=wx47d52b6420c14397&redirect_uri=" +
        encodeURIComponent(fromurl) +
        "&response_type=code&scope=snsapi_userinfo&state=getUser#wechat_redirect";
    },
    //判断微信
    isWeiXin() {
      var ua = window.navigator.userAgent.toLowerCase();
      if (ua.match(/MicroMessenger/i) == "micromessenger") {
        return true;
      } else {
        return false;
      }
    }
  }
};



